import React, { useState, useMemo, useEffect } from 'react'
import size from 'lodash/size'

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text'
import Button from '../components/Button'
import Dropdown from '../components/Dropdown'
import Pagination from '../components/Pagination'
import { responsive } from '../components/ThemeProvider/theme';

import SheetDataTable from './SheetDataTable'
import RenderTable from './RenderTable';
import hookWithLocalStorage from '../utils/hookWithLocalStorage'
import useMsal from '../contexts/msal/useMsal'

const HomePage = () => {
  const {
    account,
    loadSheets,
    loading,
    sheets,
    AuthButton,
    loadSheetData,
    sheetData,
    WorkBookIdInput,
  } = useMsal()
  const [selectedSheet, setSheet] = hookWithLocalStorage('SELECTED_SHEET')(useState)
  const [compareSheet, setCompareSheet] = useState()
  const [selectedRow, setSelectedRow] = useState({})
  const [page, setPage] = useState(0)
  const data = sheetData[selectedSheet]
  const compareSheetData = compareSheet && sheetData[compareSheet]
  const doubleHeader = useMemo(() => data && data.text && data.text[0].some(h => !h), [data])
  const headerRow = useMemo(() => {
    if (!data || !data.text) return []
    if (doubleHeader) {
      const colLength = data.text[0].length
      let groupMemo = []
      // ç©ºç½ä»£è¡¨è¦group ['a', 'b', '', 'c']
      // bå¾é¢çç©ºç½è¦è·b groupä¸èµ·ï¼æä»¥ååè½é£åï¼å¾å¾é¢æª¢æ¥åä¾ï¼åªè¦æ¯ç©ºç½ï¼å°±ç­å°ä¸ä¸åå¼åºç¾ç¶å¾æä»ågroupèµ·ä¾
      return data.text[0].slice(0).reverse().reduce((all, h, i) => {
        // æª¢æ¥ææ²æå¼
        if (h) {
          // æª¢æ¥ææ²æä¹ååæ¨è¨è¦è®ægroupç
          if (groupMemo.length) {
            // if true æç¾å¨éä¸æ ¼ä¹å å¥group
            groupMemo.push(i)
            all.push({
              Header: h,
              columns: groupMemo.slice().reverse().map(j => ({
                Header: data.text[1][colLength - j - 1],
                accessor: d => d[colLength - j - 1],
                id: `${i}-${j}`,
              })),
            })
            // æ¸ç©ºgroupçæ¨è¨
            groupMemo = []
          } else {
            // if false å°±æ¯ä¸è¬çheader
            all.push({ Header: h, accessor: d => d[colLength - i - 1], id: i })
          }
        } else {
          // æ²æå¼ï¼ä¹å¾è¦è·å¥äººgroupï¼åæ«å­èµ·ä¾
          groupMemo.push(i)
        }
        return all
      }, [])
      .reverse() // æå¾è¨å¾ååè½åä¾
      .slice(1) // ç¶å¾å»æTitle
    }
    return data.text[0].slice(1) // åæ¨£å»æTitle
  }, [data, doubleHeader])

  const tableData = useMemo(() => (data && data.text) ? data.text.slice(doubleHeader ? 2 : 1) : [], [data, doubleHeader])
  const [pages, pageStart] = useMemo(() => {
    return tableData.reduce(([all, starts], row) => {
      let i = all.length - 1
      if (row[0]) {
        i = all.push([]) - 1
        if (i) {
          starts[i] = all[i - 1].length
        }
      }
      all[i].push(row)
      return [all, starts]
    }, [[], [0]])
  }, [tableData])

  useEffect(() => {
    if (pages && pages[page]) {
      setSelectedRow(pages[page].reduce((sel, row, id) => {
        sel[id] = true;
        return sel
      }, {}))
    }
  }, [pages, page])

  const compareData = useMemo(() => (compareSheetData && compareSheetData.text) ? compareSheetData.text.slice(doubleHeader ? 2 : 1) : [], [compareSheetData, doubleHeader])
  const indexedData = useMemo(() => (data && data.text) ? data.text.reduce((all, d, i) => {
    const offset = doubleHeader ? 2 : 1
    if (i >= offset) all[i - offset] = d
    return all
  }, {}) : {}, [data, doubleHeader])

  const renderValues = useMemo(() => {
    if (!size(selectedRow)) return []
    return Object.entries(selectedRow).map(([i, selected]) => {
      return selected ? indexedData[i * 1 + pageStart[page]] : null
    }).filter(Boolean)
  }, [selectedRow, indexedData, page, pageStart])

  const updatedMark = useMemo(() => {
    if (!size(compareData)) return null
    const compareMark = compareData.reduce((all, row) => {
      all[row[0]] = row
      return all
    }, {})
    return renderValues.map((row) => {
      const toCompare = compareMark[row[0]]
      if (toCompare) {
        return row.map((r, i) => r !== toCompare[i])
      } else {
        return row.map(() => true)
      }
    })

  }, [renderValues, compareData])

  return (
    <Container py={responsive(0, '2em')}>
      {account ? (
        <Box>
          <Flex>
            <WorkBookIdInput flex="1" />
            <Box ml="1em">
              <Button disabled={loading} my="1em" onClick={loadSheets}>è¼å¥è³æè¡¨</Button>
            </Box>
          </Flex>
          {sheets && (
            <>
              <Flex>
                <Dropdown
                  width={1 / 2}
                  label="é¸æè³æè¡¨"
                  onChange={(selected) => {
                    if (selected) {
                      setSheet(selected.value)
                      loadSheetData(selected.value)
                    } else {
                      setSheet()
                    }
                  }}
                  options={sheets.map(sheet => sheet.name)}
                />
                <Dropdown
                  width={1 / 2}
                  label="å°æ¯è³æè¡¨"
                  onChange={(selected) => {
                    if (selected) {
                      setCompareSheet(selected.value)
                      loadSheetData(selected.value)
                    } else {
                      setCompareSheet()
                    }
                  }}
                  options={sheets.map(sheet => sheet.name).filter(name => name !== selectedSheet)}
                  disabled={!selectedSheet}
                />
              </Flex>
              {pages && pages[page] && (
                <>
                  <SheetDataTable
                    data={pages[page]}
                    headerRow={headerRow}
                    onSelectRow={setSelectedRow}
                  />
                  <Pagination
                    pageCount={pages.length}
                    pageRangeDisplayed={5}
                    marginPagesDisplayed={2}
                    onPageChange={({ selected }) => setPage(selected)}
                  />
                  {renderValues.length ? (
                    <RenderTable
                      allData={tableData}
                      data={renderValues}
                      headerRow={headerRow.slice(0, headerRow.length - 2)}
                      updatedMark={updatedMark}
                      page={page}
                    />
                  ) : null}
                </>
              )}
            </>
          )}
        </Box>
      ) : (
        <>
          <Text>è«åç»å¥</Text>
          <AuthButton />
        </>
      )}
    </Container>
  )
}

export default HomePage
