const PIXEL_RATIO = (function () {
  const ctx = document.createElement('canvas').getContext('2d'),
      dpr = window.devicePixelRatio || 1,
      bsr = ctx.webkitBackingStorePixelRatio ||
            ctx.mozBackingStorePixelRatio ||
            ctx.msBackingStorePixelRatio ||
            ctx.oBackingStorePixelRatio ||
            ctx.backingStorePixelRatio || 1;

  return dpr / bsr;
})();

const setHiDPICanvas = function(canvas, w, h, ratio) {
  if (!ratio) { ratio = PIXEL_RATIO; }
  const can = canvas;
  can.width = w * ratio;
  can.height = h * ratio;
  can.style.width = w + 'px';
  can.style.height = h + 'px';
  can.getContext('2d').setTransform(ratio, 0, 0, ratio, 0, 0);
}

class Dom2Canvas {
  constructor(dom, w, h, style) {
    this.dom = dom;
    this.canvas = null;
    this.ctx = null;
    this.width = w
    this.height = h;
    this.data = null;
    this.injectedStyle = style;

    this.init();
  }

  init = () => {
    if (!this.width) this.width = this.dom.clientWidth;
    if (!this.height) this.height = this.dom.clientHeight;

    // åå§åcanvas
    this.canvas = document.createElement('canvas');
    setHiDPICanvas(this.canvas, this.width, this.height)
    this.ctx = this.canvas.getContext('2d');
    // æ·»å ä¸ä¸ªé»è®¤çç©å½¢, é²æ­¢å¯¼åºæ¶, canvasçéæç.
    // this._addBackgroundRect();

    // æ·»å xmlns, æç§XHTMLæ åæ¸²æï¼å®ç°äºSVGåXHTMLçæ··åä½¿ç¨
    this._addNamespace();

    this.data = `<svg xmlns="http://www.w3.org/2000/svg" width="${this.width}" height="${this.height}">
      ${this.injectedStyle ? `<defs><style>${this.injectedStyle}</style></defs>` : ''}
      <foreignObject width="100%" height="100%">${this.dom.outerHTML}</foreignObject>
    </svg>`;
  };

  _addNamespace = () => {
    this.dom.setAttribute('xmlns', 'http://www.w3.org/1999/xhtml');
  }

  toCanvas = (cb, ...moreSVG) => {
    Promise.all([this.data, ...moreSVG].map(this.loadSVG)).then(() => {
      if (cb) {
        cb(this.canvas);
      }
    })
  };

  loadSVG = (svg) => {
    if (!svg) return null
    // hack: instead of a blobURL, if we use a dataURL, chrome seems happy...
    const url = 'data:image/svg+xml; charset=utf8, ' + encodeURIComponent(svg);
    const image = new Image();

    return new Promise((res) => {
      image.onload = () => {
        this.ctx.drawImage(image, 0, 0);
        URL.revokeObjectURL(url);
        res()
      }
      image.src = url;
    })
  }
}

export default Dom2Canvas
