import range from 'lodash/range';
import get from 'lodash/get';

import memorize from '../../utils/memorize'

const baseFontSize = 16
const emToPx = (em) => `${em * baseFontSize}px`;

export const breakpoints = [22, 36, 48, 62, 90, 120].map(emToPx);
export const containerWidth = [22, 36, 46, 58, 86].map(emToPx);
export const responsiveIndex = [
  [3, 'mobile'],
  [4, 'tablet'],
]

const responsiveMap = breakpoints.map((_, i) => {
  const id = responsiveIndex.findIndex(([ri]) => ri > i)
  return id >= 0 ? id : responsiveIndex.length
})

export const responsive = memorize((...args) => {
  const argsLen = args.length
  if (argsLen <= 1) return args[0]
  return breakpoints.map((_, i) => get(args, [responsiveMap[i]], null))
});

export const mobileOrDesktop = responsive

const font = 'Verdana, Arial, "PingFang TC", "HeiTi TC", "Microsoft JhengHei", sans-serif';

const white = '#fff';
const black = '#000';
const red = '#D6262F'
const gray = '#F0F0F0'
const darkGray = '#595959'
const blue = '#0E4E6E'
const orange = '#F78D23'
const cyan = '#19D1C3'
const steelBlue = '#93B0C2'
const pink = '#FAC2C1'
const powderBlue = '#4DACC2'
const lightSeaGreen = '#A1C6CA'
const primary = blue

export default {
  colors: {
    white,
    black,
    text: black,
    primary,
    gray,
    red,
    blue,
    darkGray,
    orange,
    cyan,
    steelBlue,
    pink,
    powderBlue,
    lightSeaGreen,
  },
  availColors: [
    'cyan',
    'steelBlue',
    'pink',
    'powderBlue',
    'lightSeaGreen',
  ],
  breakpoints,
  containerWidth,
  font,
  fontSize: `${baseFontSize}px`,
  responsive,
  zOrder: range(4).map((i) => 10 ** i),
};
