import range from 'lodash/range'
import theme from '../../components/ThemeProvider/theme'

const parenthesisPttn = /\([^)]+\)/

const xmlns = 'http://www.w3.org/2000/svg';

class Timeline {
  constructor(w, h, bounds, n = 7) {
    this.svg = document.createElementNS(xmlns, 'svg');
    this.svg.style.position = 'absolute'
    this.svg.style.top = 0
    this.svg.style.left = 0
    this.svg.setAttribute('width', w)
    this.svg.setAttribute('height', h)
    this.bounds = bounds
    const secWidth = bounds.width / 7
    range(n).forEach(i => {
      const middle = bounds.left + secWidth * (i + 0.5)
      this.addGuide(middle)
      const right = middle + secWidth / 2
      this.addGuide(right, true)
    })
  }

  addGuide = (x, thick) => {
    const line = document.createElementNS(xmlns, 'line');
    line.setAttributeNS(null, 'x1', x)
    line.setAttributeNS(null, 'y1', this.bounds.top)
    line.setAttributeNS(null, 'x2', x)
    line.setAttributeNS(null, 'y2', this.bounds.height + this.bounds.top)
    if (thick) {
      line.setAttributeNS(null, 'stroke', theme.colors.primary)
      line.setAttributeNS(null, 'opacity', 0.33)
      line.setAttributeNS(null, 'stroke-width', 2)
    } else {
      line.setAttributeNS(null, 'stroke', '#B5B5B5')
      line.setAttributeNS(null, 'stroke-width', 1)
      line.setAttributeNS(null, 'stroke-dasharray', 2)
    }
    this.svg.appendChild(line)
  }

  add = (x, y, w, h, fill) => {
    const timeline = document.createElementNS(xmlns, 'rect');
    timeline.setAttributeNS(null, 'x', x)
    timeline.setAttributeNS(null, 'y', y)
    timeline.setAttributeNS(null, 'width', w)
    timeline.setAttributeNS(null, 'height', h)
    timeline.setAttributeNS(null, 'fill', fill)
    this.svg.appendChild(timeline)
  }

  addLabel = (x, y, id) => {
    const idText = document.createElementNS(xmlns, 'text');
    idText.setAttributeNS(null, 'text-anchor', 'middle')
    idText.setAttributeNS(null, 'x', x)
    idText.setAttributeNS(null, 'y', y)
    idText.setAttributeNS(null, 'fill', 'black')
    idText.setAttributeNS(null, 'font-size', '32px')
    idText.setAttributeNS(null, 'font-family', theme.font)
    idText.setAttributeNS(null, 'font-weight', 'bold')
    const inPranthesis = parenthesisPttn.exec(id)
    if (inPranthesis) {
      idText.textContent = id.replace(inPranthesis[0], '')
      const smallText = document.createElementNS(xmlns, 'tspan');
      smallText.setAttributeNS(null, 'font-size', '22px')
      smallText.textContent = inPranthesis[0]
      idText.appendChild(smallText)
    } else {
      idText.textContent = id
    }
    this.svg.appendChild(idText)
  }

  getSVG = () => this.svg
}

export default Timeline
